@import url(https://fonts.googleapis.com/css2?family=Orbitron&family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{padding:env(safe-area-inset)}body{background-color:#495464;box-sizing:border-box;font-family:"Quicksand",sans-serif}button{-webkit-user-select:none;-ms-user-select:none;user-select:none}#drum-machine{-webkit-align-items:center;align-items:center;background-color:#E8E8E8;border-radius:0.3em;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;left:50%;padding:1em;position:absolute;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}#drum-machine #controls{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0.5rem;width:11em}#drum-machine #controls .switch-wrapper{-webkit-align-items:center;align-items:center;border:0.1em solid #BBBFCA;border-radius:0.3em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:0.1em 0.3em}#drum-machine #controls .switch-wrapper .switch{-webkit-appearance:none;appearance:none;background:#BBBFCA;border:0.2em solid #495464;height:1.6em;position:relative;transition:all 0.4s;width:3.5em}#drum-machine #controls .switch-wrapper .switch:focus{outline:1px solid #BBBFCA}#drum-machine #controls .switch-wrapper .switch::before{background-color:#14274E;border-radius:0.1em;content:'';height:1.2em;position:absolute;top:0;transition:all 0.4s;width:1.5em}#drum-machine #controls .switch-wrapper .switch:checked::before{left:52%}#drum-machine #controls [type='range']{-webkit-appearance:none;background:transparent;margin:12px 0;width:100%}#drum-machine #controls [type='range']::-moz-focus-outer{border:0}#drum-machine #controls [type='range']:focus{outline:0}#drum-machine #controls [type='range']:focus::-webkit-slider-runnable-track{background:#f5f5f5}#drum-machine #controls [type='range']:focus::-ms-fill-lower{background:#E8E8E8}#drum-machine #controls [type='range']:focus::-ms-fill-upper{background:#f5f5f5}#drum-machine #controls [type='range']::-webkit-slider-runnable-track{cursor:default;height:8px;-webkit-transition:all .2s ease;transition:all .2s ease;width:100%;box-shadow:1px 1px 1px rgba(0,0,0,0.2),0 0 1px rgba(13,13,13,0.2);background:#E8E8E8;border:2px solid #BBBFCA;border-radius:5px}#drum-machine #controls [type='range']::-webkit-slider-thumb{box-shadow:4px 4px 4px rgba(0,0,0,0.2),0 0 4px rgba(13,13,13,0.2);background:#14274E;border:2px solid #eceff1;border-radius:2px;box-sizing:border-box;cursor:default;height:24px;width:10px;-webkit-appearance:none;margin-top:-10px}#drum-machine #controls [type='range']::-moz-range-track{box-shadow:1px 1px 1px rgba(0,0,0,0.2),0 0 1px rgba(13,13,13,0.2);cursor:default;height:8px;-moz-transition:all .2s ease;transition:all .2s ease;width:100%;background:#E8E8E8;border:2px solid #BBBFCA;border-radius:5px;height:4px}#drum-machine #controls [type='range']::-moz-range-thumb{box-shadow:4px 4px 4px rgba(0,0,0,0.2),0 0 4px rgba(13,13,13,0.2);background:#14274E;border:2px solid #eceff1;border-radius:2px;box-sizing:border-box;cursor:default;height:24px;width:10px}#drum-machine #controls [type='range']::-ms-track{cursor:default;height:8px;-ms-transition:all .2s ease;transition:all .2s ease;width:100%;background:transparent;border-color:transparent;border-width:12px 0;color:transparent}#drum-machine #controls [type='range']::-ms-fill-lower{box-shadow:1px 1px 1px rgba(0,0,0,0.2),0 0 1px rgba(13,13,13,0.2);background:#dbdbdb;border:2px solid #BBBFCA;border-radius:10px}#drum-machine #controls [type='range']::-ms-fill-upper{box-shadow:1px 1px 1px rgba(0,0,0,0.2),0 0 1px rgba(13,13,13,0.2);background:#E8E8E8;border:2px solid #BBBFCA;border-radius:10px}#drum-machine #controls [type='range']::-ms-thumb{box-shadow:4px 4px 4px rgba(0,0,0,0.2),0 0 4px rgba(13,13,13,0.2);background:#14274E;border:2px solid #eceff1;border-radius:2px;box-sizing:border-box;cursor:default;height:24px;width:10px;margin-top:2px}#drum-machine #controls [type='range']:disabled::-webkit-slider-thumb,#drum-machine #controls [type='range']:disabled::-moz-range-thumb,#drum-machine #controls [type='range']:disabled::-ms-thumb,#drum-machine #controls [type='range']:disabled::-webkit-slider-runnable-track,#drum-machine #controls [type='range']:disabled::-ms-fill-lower,#drum-machine #controls [type='range']:disabled::-ms-fill-upper{cursor:not-allowed}#drum-machine #keyboard{display:grid;grid-gap:0.2em;grid-template-columns:1fr 1fr 1fr;padding:0.3em;width:13em}#drum-machine #keyboard .drum-pad{background-color:#BBBFCA;border-radius:0.3em;box-shadow:0.3em 0.3em 0.3em #14274E;font-weight:bold;line-height:4em;text-align:center;vertical-align:middle}#drum-machine #keyboard .drum-pad:active{box-shadow:inset 0 0 0.4em;color:#14274E}#drum-machine #keyboard .active{box-shadow:inset 0 0 0.4em;color:#14274E}@media screen and (max-width: 450px){#drum-machine{-webkit-flex-direction:column;flex-direction:column}}.off{pointer-events:none}.sound-info{background-color:#BBBFCA;border:0.1em solid #495464;font-family:"Orbitron",sans-serif;font-size:0.8em;line-height:2em;margin:0.5em auto;text-align:center;width:90%}.sound-info#display{height:2em}

